<template>
  <div class="product-items" v-if="products.length">
    <div v-for="(item, index) in products" :key="index">
      <router-link :to="`/category/${url}/${item._id}`" class="d-block card product-items__item text-decoration-none mb-2">
        <div class="card-body d-flex">
          <div class="product-items__item-avatar mr-2">
            <img class="mx-auto" v-if="item.image" :src="item.image"/>
          </div>
          <div class="product-items__item-content w-100 d-flex flex-column">
            <h6>{{item.title}}</h6>
            <div class="mt-auto">
              <p class="text-center text-primary m-0">На <strong>{{ item.gram || '100' }}</strong> гр. продукту</p>
              <div class="item-information d-flex text-center">
                <div class="item-information__item flex-fill d-flex flex-column">
                  <span class="item-information__header">БЖ</span>
                  <span class="item-information__footer">{{ bxbjCalc(bxCalc(item.product_b, item.gram), bjCalc(item.product_j, item.gram))}}</span>
                </div>
                <div class="item-information__item flex-fill d-flex flex-column">
                  <span class="item-information__header">У</span>
                  <span class="item-information__footer">{{ byCalc(item.product_y, item.gram)}}</span>
                </div>
                <div class="item-information__item flex-fill d-flex flex-column">
                  <span class="item-information__header">ХЕ</span>
                  <span class="item-information__footer">{{ allCalc(bxCalc(item.product_b, item.gram),bjCalc(item.product_j, item.gram),byCalc(item.product_y, item.gram))}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CategoryPage',
  data() {
    return {
      products: [],
    }
  },
  computed: {
    url() {
      return this.$route.params.id
    },
    bx_param: {
      get() {
        return this.$store.state.option.bx_param
      }
    },
    bj_param: {
      get() {
        return this.$store.state.option.bj_param
      }
    }
  },
  methods: {
    calculate(value, gram) {
      return (parseFloat(value) * gram) / 100
    },
    bxCalc(value, gram) {
      return ((this.calculate(value, gram) * parseFloat(this.bx_param)) / 12).toFixed(1);
    },
    bjCalc(value, gram) {
      return ((this.calculate(value, gram) * parseFloat(this.bj_param)) / 12).toFixed(1);
    },
    byCalc(value, gram) {
      return (this.calculate(value, gram) / 12).toFixed(1);
    },
    bxbjCalc(p1, p2) {
      return (parseFloat(p1) + parseFloat(p2)).toFixed(1);
    },
    allCalc(p1, p2, p3) {
      return (parseFloat(p1) + parseFloat(p2) + parseFloat(p3)).toFixed(1);
    },
    async getProducts() {
      try {
        await this.$pouch.find({
          selector: { category_id: this.url },
        }).then((res) => {
          this.products = res.docs;
        });
      } catch (error) {
        console.error(error)
      }
    }
  },
  mounted() {
    this.getProducts();
  }
}
</script>

<style scoped lang="scss">
.product-items {
  .card-body {
    padding: 5px;
  }
  &__item {
    color: #000000;
    &-avatar {
      width: 100px;
      min-width: 100px;
    }
    &-content{

    }
  }
  .item-information {
    &__item {
      margin: 0 2px;
      .item-information__header {
        border-bottom: 1px solid green;

      }
      .item-information__footer {
        font-weight: 500;
        font-size: 18px;
      }
    }
  }
}
</style>
